.sb-popover-overlay_container {
  border-radius: var(--sbt-border-radius-large);
  display: inline-grid;
  max-height: min(556px, 70vh);

  .sb-popover-overlay_header-clear {
    margin: 0;
  }
}

.sb-popover-overlay_elevated {
  border: 1px solid var(--sbt-theme-tertiary-color-30) !important;
  box-shadow: var(
    --sb-popover-overlay-box-shadow,
    0 1px 4px 1px rgb(186 191 199 / 40%)
  );
}

.sb-popover-overlay_small {
  max-width: var(--sb-cp-popover-overlay-max-width, 256px);
  min-width: 172px;
}

// AG-Grid doesn't handle re-sized filters well, look into this later.
.sb-popover-overlay_grid {
  max-width: 240px;
  min-width: 240px;
}

.sb-popover-overlay_standard {
  max-width: var(--sb-cp-popover-overlay_max-width, 384px);
  min-width: 256px;
}

.sb-popover-overlay_header {
  align-items: center;
  border-bottom: 1px solid var(--sbt-theme-tertiary-color-20);
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  display: flex;
  height: 40px;
  justify-content: space-between;
  overflow: hidden;
  padding: var(--sbt-spacing-30) var(--sbt-spacing-40);
}

.sb-popover-overlay_body {
  padding: var(--sbt-spacing-20) var(--sbt-spacing-40);
}

.sb-popover-overlay_footer {
  border-bottom-left-radius: var(--sbt-border-radius-large);
  border-bottom-right-radius: var(--sbt-border-radius-large);
  border-top: 1px solid var(--sbt-theme-tertiary-color-20);
  height: 56px;
  overflow: hidden;
}

.sb-popover-overlay_body-padding_none {
  padding-left: 0;
  padding-right: 0;
}

.sb-popover-overlay_footer-actions {
  --sb-button-fixed-height: 32px;
  --sb-button-padding-vertical-small: 0.2rem;
  justify-content: center;
}

.sb-popover-overlay_header-title {
  font-weight: var(--sbt-font-weight-60);
}
